import React,{Component} from 'react';
import { Card,CardImg,CardImgOverlay,CardText,CardBody,CardTitle,Breadcrumb,BreadcrumbItem,Button,Modal,ModalHeader,ModalBody,Label } from 'reactstrap';
import { Link } from 'react-router-dom';
import {LocalForm,Control,Errors} from 'react-redux-form';
import Loading from './LoadingComponent';
import {baseUrl} from '../shared/baseUrl';
import { FadeTransform, Fade, Stagger } from 'react-animation-components';

const required = (val) => val && val.length;
const maxLength = (len) => (val) => !(val) || (val.length <= len);
const minLength = (len) => (val) => (val) && (val.length >= len);
class Comment extends Component {
    constructor(props){
        super(props);

        this.state = {
            isModalOpen:false
        }
        this.toggleModal = this.toggleModal.bind(this);

        
        
    }
    toggleModal(){
        this.setState({
            isModalOpen : !this.state.isModalOpen
        })
    }
    handleSubmit(values){
        this.toggleModal(); 
        this.props.postComment(this.props.dishId,values.rating,values.author,values.comment);
    }
    render() {
        return (
            <div>
                 <Button outline onClick={this.toggleModal}>
                           <span className="fa fa-pencil fa-lg"></span> Submit Comment
                       </Button>
                 <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
             <ModalHeader toggle={this.toggleModal}>Submit Comment</ModalHeader>
             <ModalBody>
                 <LocalForm onSubmit={(values) => this.handleSubmit(values)}>
                     <div className="form-group">
                         <Label htmlFor="rating">Rating</Label>
                         <Control.select model=".rating" id="rating" className="form-control" name="rating">
                             <option>1</option>
                             <option>2</option>
                             <option>3</option>
                             <option>4</option>
                             <option>5</option>
                         </Control.select>
                     </div>
                     <div className="form-group">
                         <Label htmlFor="name">Your Name</Label>
                         <Control.text validators={{
                                    required,minLength : minLength(3),maxLength : maxLength(15)
                                }} model='.author' id="author" name="author" placeholder="Your Name" className="form-control"/>
                         <Errors
                                className="text-danger" model=".author" show="touched" messages={{
                                    required:'Required',
                                    minLength:'Must be greater than 2 characters',
                                    maxLength:'Must be 15 characters or less'
                                }}/>
                     </div>
                     <div className="form-group">
                         <Label htmlFor="name">Comment</Label>
                         <Control.textarea model='.comment' rows="6" id="comment" name="comment" className="form-control"/>
                     </div>
                     <div className="form-group">
                     <Button type="submit" value="submit" color="primary">Submit</Button>
                     </div>
                 </LocalForm>
             </ModalBody>
         </Modal>
            </div>
        );
    }
}

   
    function RenderDishDetail({dish}){
        if(dish != null){
            return (
            <FadeTransform in transformProps={{
                exitTransform:'scale(0.5) translateY(-50%)'
            }}><Card>
            <CardImg width="100%" src={baseUrl+dish.image} alt={dish.name} />
            <CardBody>
                <CardTitle>{dish.name}</CardTitle>
    <CardText>{dish.description}</CardText>
            </CardBody>
        </Card></FadeTransform>)
        }
        else{
            return <div></div>
        }
    }

    function RenderComments({comments,postComment,dishId}){
        if(comments != null){
            return <div>
                <Stagger in>
                {comments.map((comment)=>(
                <div key={comment.id}>
                <ul className="list-unstyled">
                    <Fade in>
                    <li>{comment.comment}</li>
            <li>--{comment.author} , {new Intl.DateTimeFormat('en-US',{year:'numeric',month:'short',day:'2-digit'}).format(new Date(Date.parse( comment.date)))}</li>
            </Fade></ul>
                </div>
           
              
            ))}
            </Stagger>
            <Comment postComment={postComment} dishId={dishId}/></div> ;
        }
        else{
            return <div></div>
        }
    }
    const DishdetailComponent = (props)=>{
        if(props.isLoading){
            return (
                <div className="container">
                    <div className="row">
                        <Loading/>
                    </div>
                </div>
            );
        }
        else if(props.errMess){
            return (
                <div className="container">
                    <div className="row">
            <h4>{props.errMess}</h4>
                    </div>
                </div>
            );
        }
        else if(props.dish != null){
            return (
                <div className="container">
                    <div className="row">
                    <Breadcrumb>
                    <BreadcrumbItem><Link to='/home'>Home</Link></BreadcrumbItem>
                    <BreadcrumbItem><Link to='/menu'>Menu</Link></BreadcrumbItem>
                    <BreadcrumbItem>{props.dish.name}</BreadcrumbItem>
                    </Breadcrumb>
                    <div className="col-12">
                        <h3>Menu</h3>
                        <hr/>
                    </div>
                </div>
                <div className="row">
                        <div className="col-md-5 m-1"><RenderDishDetail dish={props.dish} />
                    </div>
                    <div className="col-md-5 m-1">
                    <h4>Comments</h4>
                        <RenderComments postComment = {props.postComment} dishId = {props.dishId} comments={props.comments}/>
                       
                    </div>
                    </div>
                    </div>
            );
        }
        else{
            return <div></div>
        }
    }
        
        
    


export default DishdetailComponent;